@import url(https://fonts.googleapis.com/css?family=Cardo:400i|Rubik:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #282c34;
}
.App {
  text-align: center;
}

.Header_header__3tFrN {
  padding: 5px;
  background-color: #282c34;
  text-align: center;
}

.Header_header__3tFrN h2 {
  color: #4C3F76;
}

.Header_header__3tFrN h2 span {
  color: deepskyblue;
}

.Header_header-content__2GYoD {
  background-color: #5B6471;
  display: grid;
  grid-template-columns: auto 1fr 3fr;
  align-items: center;
  border-radius: 5px;
  font-size: 1.5rem;
}

.Header_header-logo__35HaG {
  height: 2.5rem;
  margin: 0 20px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .Header_header-logo__35HaG {
    animation: Header_header-logo-spin__3Xybm infinite 15s linear;
  }
}

@keyframes Header_header-logo-spin__3Xybm {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

:root {
  --d: 700ms;
  --e: cubic-bezier(0.19, 1, 0.22, 1);
  --font-sans: "Rubik", sans-serif;
  --font-serif: "Cardo", serif;
}

* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  display: grid;
}

.MainBody_page-content__QFnPU {
  display: grid;
  grid-gap: 1rem;
  padding: 1rem;
  max-width: 1024px;
  margin: 0 auto;
  font-family: "Rubik", sans-serif;
  font-family: var(--font-sans);
}
@media (min-width: 600px) {
  .MainBody_page-content__QFnPU {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 800px) {
  .MainBody_page-content__QFnPU {
    grid-template-columns: repeat(4, 1fr);
  }
}

.MainBody_d_card__nlOxp {
  background-color: #5B6471 !important;
  display: flex;
  flex-direction: column;
  -webkit-box-align: end;
  overflow: hidden;
  padding: 1rem;
  width: 95%;
  text-align: center;
  color: whitesmoke;
  background-color: whitesmoke;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1), 0 2px 2px rgba(0, 0, 0, 0.1),
    0 4px 4px rgba(0, 0, 0, 0.1), 0 8px 8px rgba(0, 0, 0, 0.1),
    0 16px 16px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
}
@media (min-width: 600px) {
  .MainBody_d_card__nlOxp {
    height: 400px;
    background-size: contain !important;
  }
}

.MainBody_loading__PwWgp {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #5B6471;
  display: flex;
  justify-content: center;
  align-items: center;
}

.MainBody_error__3tWMt{
  text-align: center;
  color: red;
}

.MainBody_content__2CWMh {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.MainBody_heading__OMSPA {
  font-size: 1.3rem;
  font-weight: bold;
  line-height: 1.2;
}

.MainBody_data-content__2EFrB {
  font-family: "Cardo", serif;
  font-family: var(--font-serif);
  font-size: 1.125rem;
  font-style: italic;
  line-height: 1.35;
}

.MainBody_d_card__nlOxp > span > img {
  display: grid;
  width: 100%;
  height: auto;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1), 0 2px 2px rgba(0, 0, 0, 0.1),
  0 4px 4px rgba(0, 0, 0, 0.1), 0 8px 8px rgba(0, 0, 0, 0.1),
  0 16px 16px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
}
.LoadingSpinner_spinner__2gZuX {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.LoadingSpinner_spinner__2gZuX:after {
  content: ' ';
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid deepskyblue;
  border-color: deepskyblue transparent deepskyblue transparent;
  animation: LoadingSpinner_spinner__2gZuX 1.2s linear infinite;
}
@keyframes LoadingSpinner_spinner__2gZuX {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
